const mongoose = require('mongoose');

const {
    ObjectId
} = mongoose.Schema;

const DataColChatSchema = mongoose.Schema({
    text: {
        type: String,
        required: true,
    },
    user: {
        type: ObjectId,
        ref: 'User',
    },
    room: {
        type: ObjectId,
        ref: 'SimSimRoom',
    },
    bot: {
        type: Boolean,
        default: false,
    },


    date: {
        type: Date,
        default: Date.now(),
    },
});


const model = mongoose.model('DataColChat', DataColChatSchema);

export default model;